.rating-container {
    background-color: rgb(58, 58, 58);
    padding: 5px 5px 2px 5px;
    border-radius: 5px;
    width: 210px;
    margin: 0 auto;

    .rating {
        background-color: yellow;
        opacity: 0.5;
        height: 40px;
        width: 20px;

        &.rating-hover {
            opacity: 1;
        }
    }

    .half, .full {
        display: inline-block;
        height: 40px;
        width: 20px;
        cursor: pointer;
    }

    .half > div {
        clip-path: polygon(100% 0%, 104% 20%, 104% 50%, 100% 70%, 42% 91%, 64% 57%, 4% 35%, 78% 35%);
        --webkit-clip-path: polygon(100% 0%, 104% 20%, 104% 50%, 100% 70%, 42% 91%, 64% 57%, 4% 35%, 78% 35%);
    }
      
    .full > div {
        -webkit-clip-path: polygon(0% 0%, 18.5% 35%, 98% 35%, 34% 57%, 53.50% 91%, 0% 70%);
        clip-path: polygon(0% 0%, 18.5% 35%, 98% 35%, 34% 57%, 53.50% 91%, 0% 70%);
    }
}
